
const Utils = {
  getFormUrl: (psid, tag) => {
    const euc = encodeURIComponent;
    return `https://docs.google.com/forms/d/e/1FAIpQLSds9-as5kOcCr9_gtQ-4xic8-CTSmsQaHEajU-OX4xGpz8wHQ/viewform?usp=pp_url&entry.450789842=${euc(psid)}${tag ? '&entry.1699976597=' + euc(tag) : ''}`;
  },

  getParameterByName: (name) => {
    const url = window.location.href;
    /*eslint-disable */
    name = name.replace(/[\[\]]/g, "\\$&");
    /*eslint-enable */
    var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)");
    var results = regex.exec(url);
    if (!results) return null;
    if (!results[2]) return '';
    return decodeURIComponent(results[2].replace(/\+/g, " "));
  },

  getPsid: () => {
    const _id = Utils.getParameterByName('psid_hashed') || '';
    const _psid = _id.match(/[0-9]{3,20}[A-Z]{3,10}/);
    const psid = _psid ? _psid[0] : 'none';
    return psid;
  }
}

module.exports = Utils;